import * as React from 'react';
import styled from 'styled-components';
const Container = styled.span`
  padding-top: 2px;
  display: flex;
`;
const Wrapper = styled.span`
  display: inline-block;
  background-color: rgba(17,31,44,0.08);
  height: 12px;
  margin-top: 2px;
  border-radius: 5px;
`;

// String.fromCharCode(160) 空格占位符
const SPACE_CHAT = String.fromCharCode(160);

export const LoadingWrapper: React.FC<{ widthRange: string }> = (props) => {
  const { widthRange } = props;
  return (
    <Container>
      <Wrapper style={{ width: widthRange }} >{SPACE_CHAT}</Wrapper>
    </Container>
  );
};